.services {
	margin: 21px auto;
	padding: 0 16px;
	background-color: #fff;

	@media (min-width: 1360px) {
		margin: 32px auto 26px;
	}

	&__title {
		margin: 0 auto 21px;

		@media (min-width: 1360px) {
			margin: 0 auto 35px;
		}
	}

	&__btn-wrap {
		text-align: center;
	}

	&__cost-btn {
		width: 100%;
		margin: 10px auto;
		border: none;
		padding: 10px;
		background-color: #454647;
		color: #fff;
		font-family: 'Raleway', sans-serif;
		font-weight: 400;
		font-size: 14px;
		line-height: 16px;
		text-align: center;
		letter-spacing: .25px;

		@media (min-width: 1360px) {
			width: auto;
			min-width: 379px;
			margin: 15px auto;
			font-size: 20px;
			line-height: 23px;
		}
	}
}